"use client";

import React from "react";
import {useState} from "react"

const Nav = ()=>{
    const [toggle, setToggle]=useState(false)
    const menu=()=>{
        setToggle(!toggle)
    }
    return(
        <div>

            <div className="flex justify-around ">
                  <a href="">Home</a>
                 
                <div>
                     <button onClick={menu}>Menu</button>
                    
                    {toggle&&(
                        <div className="flex">
                            <a href="">Fish</a>
                        </div>
                    )}

                </div>
                   <a href="">Nutrition and Ingredients</a>
            
                    <a href="">About Us</a>

                

            </div>
        </div>
    )
}

export default Nav